/*
**  define styling for element component with specified with classe  main header.
*/
.header {
	&.sticky {
		opacity: 0.98;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 1000;
	}

	background-color: var(--color-primary);
	padding: 1rem 3rem;

	display: flex;
	justify-content: space-between;
	align-items: center;
	color: var(--color-bg-primary);

	.logo {
		color: inherit;
		text-decoration: none;
		font-size: 3rem;
		// font-style: italic;
		font-weight: 900;
		letter-spacing: 2px;
	}

	.main-nav {
		// flex-grow: 1;
		margin-left: auto;

		&__list {
			display: flex;
			align-items: center;
			gap: 3rem;

			@include rwd-df(lg) {
				position: fixed;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				transition: all 0.3s ease;
				transform: translate(100%);
				opacity: 0;
				z-index: 100000;

				background-color: var(--color-primary);
				padding: 5rem 0;
				flex-direction: column;
				align-items: center;
				gap: 0;
			}

			&.show {
				@include rwd-df(lg) {
					transform: translate(0);
					opacity: 1;
				}
			}

			&__item {
				@include rwd-df(lg) {
					flex-grow: 1;
					display: flex;
					align-items: center;
					justify-content: center;
				}
				&__link {
					font-size: 2rem;
					font-weight: 700;
					color: inherit;
					text-decoration: none;
					transition: all 0.3s ease;

					@include rwd-df(lg) {
						display: block;
						width: 100%;
						height: 100%;
						// background-color: #fff;
					}

					&:link,
					&:visited {
						opacity: 0.6;
					}

					&:hover,
					&:active,
					&.active {
						opacity: 1;
						text-decoration: underline;
					}
				}

				&__mode-btn {
					font-size: 2rem;
					color: inherit;
					cursor: pointer;

					&__icon {
						font-size: 2.5rem;
						opacity: 0.6;
						transition: all 0.3s ease;

						&:hover {
							opacity: 1;
						}

						&--dark {
							display: none;
						}
					}
				}
			}
		}

		&__toggle-btn {
			display: none;
			font-size: 3rem;
			cursor: pointer;
			transition: all 0.3s ease;
			position: relative;
			z-index: 100001;

			&__icon {
				&.fa-bars {
					display: block;
				}

				&.fa-times-circle {
					display: none;
				}
			}

			&:hover {
				transform: scale(1.3);
			}

			@include rwd-df(lg) {
				display: block;
			}
		}
	}
}

html.light-theme {
	.main-nav__list__item__mode-btn__icon {
		&--dark {
			display: none;
		}

		&--light {
			display: block;
		}
	}
}

html.dark-theme {
	.main-nav__list__item__mode-btn__icon {
		&--light {
			display: none;
		}

		&--dark {
			display: block;
		}
	}
}
