.tooltip {
	position: relative;
	display: inline-block;
	text-decoration: underline;
	font-size: 1.8rem;
	font-weight: 600;
	font-style: italic;

	&__info {
		font-size: 1.6rem;
		font-weight: normal;
		font-style: normal;
		display: none;
		text-align: center;
		// background-color: rgb(141, 135, 135);
		background-color: var(--color-tertiary);
		// color: rgba($color-white, $alpha: 0.9);
		color: var(--color-bg-primary);
		padding: 0.3rem 0.8rem;
		border-radius: 0.5rem;
		position: absolute;
		z-index: 2;
	}

	&:hover &__info {
		display: inline-block;
		&::after {
			content: '';
			border: 0.8rem solid transparent;
			position: absolute;
			z-index: 3;
		}
	}

	&[data-direction='top']:hover &__info {
		top: -1.6rem;
		left: 50%;
		transform: translate(-50%, -100%);

		&::after {
			border-top-color: var(--color-tertiary);
			top: 100%;
			left: 50%;
			transform: translateX(-50%);
		}
	}

	&[data-direction='bottom']:hover &__info {
		top: calc(100% + 1.6rem);
		left: 50%;
		transform: translateX(-50%);

		&::after {
			border-bottom-color: var(--color-tertiary);
			top: -1.6rem;
			left: 50%;
			transform: translateX(-50%);
		}
	}

	&[data-direction='start']:hover &__info {
		top: 50%;
		left: -1.8rem;
		transform: translate(-100%, -50%);

		&::after {
			border-#{$start}-color: var(--color-tertiary);
			top: 50%;
			left: 100%;
			transform: translateY(-50%);
		}
	}

	&[data-direction='end']:hover &__info {
		top: 50%;
		left: calc(100% + 1.8rem);
		transform: translateY(-50%);

		&::after {
			border-#{$end}-color: var(--color-tertiary);
			top: 50%;
			left: 0;
			transform: translate(-100%, -50%);
		}
	}
}

