.sec--contacts {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		justify-content: space-between;
		gap: 5rem;

		@include rwd-df(lg) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__map {
		border: 0.2rem solid var(--color-primary);
		padding: 0.3rem;
		border-radius: 0.4rem;

		iframe {
			display: block;
			width: 100%;
			height: 30rem;
		}
	}
}

.form {
	font-family: $main-font;
	display: flex;
	flex-direction: column;
	gap: 2rem;

	&__input,
	&__textarea {
		display: block;
		width: 100%;
		font-size: 1.5rem;
		font-family: inherit;
		color: var(--color-primary);
		font-weight: 700;
		// border-radius: 1rem;

		padding: 1.5rem 2rem;
		border-radius: 0.5rem;
		// background-color: rgba($color-white, 0.5);
		// background-color: var(--color-tertiary);
		// border: 0.2rem solid $color-grey-light-1;
		border: 0.2rem solid var(--color-primary);
		transition: all 0.3s;

		&::-webkit-input-placeholder {
			// color: $color-grey-light-1;
			color: var(--color-black);
		}

		&:focus {
			outline: none;
			// box-shadow: 0 1rem 2rem rgba($color-black, 0.1);
			box-shadow: 0 5px 3px var(--color-tertiary);
			// border: 0.2rem solid $color-green-light;
			border: 0.2rem solid var(--color-tertiary);
		}

		&:focus:invalid {
			// border: 0.2rem solid $color-orange-dark;
			border: 0.2rem solid var(--color-secondary);
		}

		&.error {
			margin-top: 0 !important;
		}
	}

	label.error {
		font-family: inherit;
		color: var(--color-secondary);
		font-size: 1.2rem;
		font-weight: 700;
		display: inline-block;
		padding-left: 2rem;
		margin-top: 0.7rem;
		display: block;
	}

	&__textarea {
		resize: none;
	}

	&__submit-btn {
		padding: 1rem 3rem;
		border-radius: 1rem;
		text-transform: uppercase;
		// color: rgba($color-white, 0.8);
		color: var(--color-bg-primary);
		letter-spacing: 2px;
		font-weight: 600;
		cursor: pointer;
		// background-color: rgba($color-black, 0.5);
		background-color: var(--color-primary);

		transition: all 0.3s ease;
		outline: none;

		&:hover {
			// color: rgba($color-black, 0.5);
			color: var(--color-bg-primary);
			background-color: var(--color-primary);
			box-shadow: 0 0 3px 3px var(--color-text-primary);
		}

		@include rwd-df(sm) {
			display: block;
			margin: 0 auto;
		}
	}
}
