.statistics {
	&__content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		column-gap: 5rem;

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			row-gap: 4rem;
		}
	}

	&__item {
		padding: 3rem;
		border-radius: 1rem;
		// box-shadow: 0 0 2px 2px rgba($color-black, 0.5);
		box-shadow: 0 0 2px 2px var(--color-tertiary);
		text-align: center;
		&__icon {
			margin-bottom: 3rem;
			font-size: 5rem;
			// color: rgba($color-black, 0.5);
			color: var(--color-tertiary);
		}
		&__number {
			font-size: 5rem;
			display: block;
			color: var(--color-secondary);
		}
		&__title {
			font-style: italic;
			font-size: 4rem;
			// color: rgba($color-black, 0.8);
			color: var(--color-primary);
		}
	}
}
