.courses {
	&__titles-list {
		display: flex;
		gap: 0.3rem;
		margin-bottom: 1rem;
		&__item {
			font-weight: 600;
			cursor: pointer;
			border-radius: 0.5rem;
			text-align: center;
			padding: 1rem;
			flex: 1 1 auto;

			// background-color: rgba($color-black, 0.1);
			background-color: var(--color-primary);
			opacity: 0.8;
			color: var(--color-bg-primary);

			transition: all 0.3s ease-in;

			&:hover,
			&.active {
				// background-color: rgba($color-black, 0.4);
				background-color: var(--color-primary);
				opacity: 1;
				// color: rgba($color-white, 0.9);
				color: var(--color-bg-primary);
			}
		}
	}

	&__data-list {
		padding: 1rem;
		border-radius: 0.5rem;
		// background-color: rgba($color-black, 0.4);
		background-color: var(--color-primary);
		&__item {
			// color: rgba($color-white, 0.9);
      color: var(--color-bg-primary);
			display: none;

			&.show {
				display: block;
			}
			&--1 {
			}
		}
	}
}
