.spinner-container {
	// background-color: $color-primary;
	background-color: var(--color-primary);
	position: fixed;
	z-index: 100000;
	height: 100vh;
	width: 100vw;
	opacity: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.5s ease-in-out;
}

.page-content-loaded .spinner-container {
	visibility: hidden;
	opacity: 0;
}

.spinner {
	width: 40px;
	height: 40px;
	position: relative;
	animation: spinner-animation 2.5s infinite linear both;

	&__dot {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		animation: spinner-animation-dot 2s infinite ease-in-out both;

		&:before {
			content: '';
			display: block;
			width: 25%;
			height: 25%;
			background-color: #fff;
			border-radius: 100%;
			animation: spinner-animation-dot-before 2s infinite ease-in-out both;
		}

		&:nth-child(1) {
			animation-delay: -1.1s;
		}
		&:nth-child(2) {
			animation-delay: -1s;
		}
		&:nth-child(3) {
			animation-delay: -0.9s;
		}
		&:nth-child(4) {
			animation-delay: -0.8s;
		}
		&:nth-child(5) {
			animation-delay: -0.7s;
		}
		&:nth-child(6) {
			animation-delay: -0.6s;
		}
		&:nth-child(1):before {
			animation-delay: -1.1s;
		}
		&:nth-child(2):before {
			animation-delay: -1s;
		}
		&:nth-child(3):before {
			animation-delay: -0.9s;
		}
		&:nth-child(4):before {
			animation-delay: -0.8s;
		}
		&:nth-child(5):before {
			animation-delay: -0.7s;
		}
		&:nth-child(6):before {
			animation-delay: -0.6s;
		}
	}
}

@keyframes spinner-animation {
	100% {
		transform: rotate(360deg);
	}
}

@keyframes spinner-animation-dot {
	80%,
	100% {
		transform: rotate(360deg);
	}
}

@keyframes spinner-animation-dot-before {
	50% {
		transform: scale(0.4);
	}
	100%,
	0% {
		transform: scale(1);
	}
}

