.generate {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	column-gap: 1rem;

	@include rwd-df(md) {
		grid-template-columns: 1fr;
		row-gap: 4rem;
	}

	&__code {
		text-align: center;
		&__info {
			font-size: 4rem;
			font-weight: 600;
			font-style: italic;
			letter-spacing: 0.5rem;
			margin: 0 auto 3rem;
			padding: 0.3rem 4rem;
			color: var(--color-tertiary);
			// background-color: cyan;
			// background-color: var(--color-tertiary);
			// border: 1px solid rgba(yellow, 0.5);
			// border: 1px solid var(--color-secondary);
			width: fit-content;

			&::before {
				content: '#';
			}
		}
	}

	&__quote {
		text-align: center;
		&__info {
			font-size: 2rem;
			// color: rgba($color-black, 0.6);
			color: var(--color-tertiary);

			&:before,
			&:after {
				font-size: 2.5rem;
				font-weight: 900;
				// color: rgba($color-black, 0.6);
				color: var(--color-tertiary);
				opacity: 0.6;
			}

			&:before {
				content: '\201c';
				margin-#{$end}: 1rem;
			}

			&:after {
				content: '\201d';

				margin-#{$start}: 1rem;
			}
		}
		&__author {
			font-style: italic;
			font-weight: 400;
			text-transform: uppercase;
			letter-spacing: 2px;
			margin-bottom: 3rem;
			// color: rgba($color-black, 0.8);
			color: var(--color-tertiary);
			opacity: 0.8;
			// text-align: $end;

			&::before {
				content: '\2014';
				font-weight: 900;
				margin-#{$end}: 0.5rem;
				color: currentColor;
			}
		}

		&__btn {
		}
	}

	&__btn {
		padding: 1rem 3rem;
		border-radius: 1rem;
		text-transform: uppercase;
		// color: rgba($color-white, 0.8);
		color: var(--color-bg-primary);
		letter-spacing: 2px;
		font-weight: 600;
		cursor: pointer;
		// background-color: rgba($color-black, 0.5);
		background-color: var(--color-primary);

		transition: all 0.3s ease;
		outline: none;

		&:hover {
			// color: rgba($color-black, 0.5);
			color: var(--color-bg-primary);
			background-color: var(--color-primary);
			box-shadow: 0 0 3px 3px var(--color-text-primary);
		}
	}
}
