.gallery {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	justify-content: space-between;
	gap: 1.5rem;

	@include rwd-df(lg) {
		grid-template-columns: repeat(4, 1fr);
	}

	@include rwd-df(sm) {
		grid-template-columns: repeat(3, 1fr);
	}

	@include rwd-df(xs) {
		grid-template-columns: repeat(2, 1fr);
	}

	&__item {
		position: relative;
		overflow: hidden;
		cursor: pointer;
		border-radius: 1rem;
		// box-shadow: 0 0 0px 1px rgba($color-black, 0.1);
		box-shadow: 0 0 2px 1px var(--color-tertiary);
		transition: all 0.4s ease-in;

		&__img {
			object-fit: cover;
			transition: all 0.4s ease-in;
		}

		&:hover &__img {
			filter: brightness(0.7) blur(1px);
		}

		&__maximize-icon {
			@include absCenter;
			transform: translate(-50%, -50%) scale(0);
			transform-origin: center;
			visibility: hidden;
			opacity: 0;
			font-size: 5rem;
			// color: rgba($color-white, 0.95);
			color: var(--color-white);
			transition: all 0.4s ease-in;
		}

		&:hover {
			// box-shadow: 0 0 7px 1px rgba($color-black, 0.95);
			box-shadow: 0 0 7px 1px var(--color-tertiary);
		}

		&:hover &__maximize-icon {
			transform: translate(-50%, -50%) scale(1.5);
			visibility: visible;
			opacity: 1;
		}
	}
}
