.internet-notify {
	background-color: #ccc;
	display: flex;
	align-items: center;

	position: fixed;
	z-index: 100000;
	left: 0.5rem;
	bottom: 3.5rem;

	visibility: visible;
	opacity: 1;
	transition: all 1s ease-in-out;

	padding: 1rem;
	border-radius: 1rem;
	border: 2px solid $color-primary;

	&.hidden {
		visibility: hidden;
		opacity: 0;
	}

	.fa-stack {
		font-size: 0.8em;
	}

	&__icon {
		color: green;
	}

	&__text {
		margin: 0rem 0.5rem;
	}

	&__reload-btn {
		cursor: pointer;
		border-radius: 5px;
		padding: 0.3rem 0.6rem;
		text-decoration: underline;
		transition: all 0.3s ease-in-out;

		&:hover {
			text-decoration: none;
			transform: translateY(-1px);
			box-shadow: 0 0px 3px rgba($color-black, 0.1);
		}

		&:focus {
			transform: translateY(0);
			text-decoration: underline;
		}
	}
}

.close-internet-notify {
	cursor: pointer;
}
