.sec {
	padding: 8rem 0;
}

.sec--tabs__content {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	column-gap: 4rem;

	@include rwd-df(md) {
		grid-template-columns: 1fr;
		row-gap: 4rem;
	}
}
