:root {
	--color-bg-primary: #fff;
	--color-text-primary: #303030;
	--color-primary: #1272cc;
	--color-secondary: #ba2323;
	--color-tertiary: #303030;

	--color-white: #fff;
	--color-black: #000;
}

.light-theme {
	--color-bg-primary: #fff;
	--color-text-primary: #303030;
	--color-primary: #1272cc;
	--color-secondary: #ba2323;
	--color-tertiary: #303030;
}

.dark-theme {
	--color-bg-primary: #303030;
	--color-text-primary: #fff;
	--color-primary: #a2d2ff;
	--color-secondary: #fa8d8d;
	--color-tertiary: #d7dde5;
}

