.scrol-to-top {
	// background-color: darkseagreen;
	background-color: var(--color-secondary);

	width: 4rem;
	height: 4rem;
	border-radius: 50%;
	cursor: pointer;
	// box-shadow: 0 0 2px 3px rgba($color-black, 0.4);
	box-shadow: 0 0 2px 3px var(--color-tertiary);

	position: fixed;
	z-index: 100;
	#{$end}: 2rem;
	bottom: 5rem;

	display: flex;
	justify-content: center;
	align-items: center;

	transition: all 0.5s ease-in-out;

	&.hidden {
		visibility: hidden;
		opacity: 0;
	}

	&:hover {
		transform: translateY(-3px);
	}

	&:focus {
		transform: translateY(3px);
	}
}
