.modal {
	&__backdrop {
		transform: scale(1);
		position: fixed;
		z-index: 10000;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;

		visibility: visible;
		opacity: 1;
		background-color: rgba(200, 200, 200, 0.85);
		z-index: 1000;
		transition: all 0.3s ease-in-out;

		&.hidden {
			transform: scale(0);
			visibility: hidden;
			opacity: 0;
		}
	}

	&__content {
		// background-color: lightskyblue;
		// box-shadow: 0 0 3px 1px rgba($color-black, 0.75);
		box-shadow: 0 0 3px 1px var(--color-tertiary);
		width: auto;
		max-height: 90vh;
		overflow: hidden;
		@include absCenter;
		border-radius: 1rem;
		z-index: 10001;

		@include rwd-df(md) {
			width: 90vw;
			height: auto;
		}

		&__close-icon-container {
			position: absolute;
			#{$end}: 1rem;
			top: 1rem;
			display: block;
			z-index: 10002;
			cursor: pointer;
			transition: all 0.3s ease-in;
			// color: rgba($color-black, 0.7);
			color: var(--color-tertiary);

			&:hover {
				transform: translateY(-1px) scale(1.2);
				// color: rgba($color-black, 0.95);
			}

			&:focus {
				transform: translateY(1px) scale(1.2);
				// color: rgba($color-black, 0.95);
			}
		}

		&__img {
			width: auto;
			height: 100%;
			object-fit: cover;

			@include rwd-df(md) {
				width: 100%;
				height: auto;
			}
		}
	}
}
